.wsgy-react-wave-animation-animating {
	position: relative
}

.wsgy-react-wave-animation-animating:before {
	content: "";
	display: block;
	background-color: inherit;
	pointer-events: none;
	position: absolute;
	z-index: 1;
	top: -1px;
	left: -1px;
	bottom: -1px;
	right: -1px;
	border-radius: inherit;
	border: 0 solid;
	border-color: inherit;
	opacity: .3;
	-webkit-animation: wsgy-react-wave-animation .3s ease-in-out forwards;
	animation: wsgy-react-wave-animation .3s ease-in-out forwards;
	-ms-flex-negative: 0;
	flex-shrink: 0
}

@-webkit-keyframes wsgy-react-wave-animation {
	to {
		top: -6px;
		left: -6px;
		bottom: -6px;
		right: -6px;
		border-width: 6px;
		opacity: 0
	}
}

@keyframes wsgy-react-wave-animation {
	to {
		top: -6px;
		left: -6px;
		bottom: -6px;
		right: -6px;
		border-width: 6px;
		opacity: 0
	}
}

