.wsgy-lightup-img-wrapper>.wsgy-lightup-img {
	width: 100%;
	height: 100%;
	border-radius: 4px
}

.wsgy-lightup-scale {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: transparent;
	z-index: -9999;
	pointer-events: none
}

.wsgy-scroll-wrapper {
	width: 100%;
	height: 100%;
	overflow-y: hidden;
	position: relative
}

.wsgy-scroll-wrapper .wsgy-scroll-container {
	-webkit-transition: -webkit-transform 50ms ease;
	transition: -webkit-transform 50ms ease;
	-o-transition: transform 50ms ease;
	transition: transform 50ms ease;
	transition: transform 50ms ease, -webkit-transform 50ms ease;
	transition: transform 50ms ease,-webkit-transform 50ms ease
}

.wsgy-scroll-wrapper .wsgy-scroll-y-track {
	position: absolute;
	top: 0;
	right: 0;
	height: 100%;
	background-color: rgba(0,0,0,.04);
	display: none
}

.wsgy-scroll-wrapper .wsgy-scroll-y-track.y-visible {
	display: block
}

.wsgy-scroll-wrapper .wsgy-scroll-y-track .wsgy-scroll-bar {
	width: 14px;
	height: 80px;
	max-height: 100%;
	-webkit-transition: -webkit-transform 50ms ease;
	transition: -webkit-transform 50ms ease;
	-o-transition: transform 50ms ease;
	transition: transform 50ms ease;
	transition: transform 50ms ease, -webkit-transform 50ms ease;
	transition: transform 50ms ease,-webkit-transform 50ms ease;
	background-color: #c1c1c1
}


